body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f7f9fc;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #4caf50;
  padding: 10px 20px;
  color: white;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.navbar ul li {
  display: inline;
}

.navbar ul li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.welcome-section, .progress-section, .reflection-section, .motivation-section {
  text-align: center;
  padding: 40px 20px;
}

.welcome-section img, .motivation-section img {
  width: 100%;
  max-width: 800px;
  height: auto;
  border-radius: 10px;
  margin-top: 20px;
}

.form-link {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1.2em;
}

.form-link:hover {
  background-color: #0056b3;
}

footer {
  text-align: center;
  padding: 10px;
  background-color: #4caf50;
  color: white;
}

